* {
  margin: 0px;
}

body {
  background-color: #ccc;
}

.logo img {
  max-height: 90px;
  border-radius: 49px;
}

.header nav {
  background-color: rgb(124, 124, 124);
  color: rgb(247, 138, 138);
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 25px;
}

.header nav ul {
  flex-basis: 90%;
  list-style: none;
  display: flex;
  justify-content: flex-end;
}

.header nav ul li {
  margin: 0 20px;
  max-height: 16px;
}

.header nav a {
  color: rgb(204, 204, 204);
  text-decoration: none;
  margin: 0 10px;
  opacity: 0.7;
  transition: all 300ms;
}

.header nav a:hover {
  color: lightblue;
  opacity: 1;
  font-size: 20px;
}

.content {
  margin: 25px auto;
  width: 75%;
  min-height: 250px;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 15px;
  padding: 25px;
}

.loginForm input {
  display: block;
  margin: 15px auto;
  padding: 15px;
  border-radius: 5px;
  background-color: aliceblue;
}

.error {
  width: 20%;
  margin: 15px auto;
  padding: 15px;
  background-color: #8e9295;
  color: red;
  border-radius: 5px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}